import {IActionModel, ActionType} from '../models/action.model';
import {MethodType} from '../models/interface.model';

const dataSchema = {
    type: "object",
    required: ["key", "title", "icon"],
    properties: {
        key: {
            type: "string",
            title: "KEY"
        },
        type: {
            type: "string",
            title: "类型"
        },
        text: {
            type: "object",
            title: "SCHEMA配置"
        },
        description: {
            type: "string",
            title: "描述"
        }
    }
};

class List {
    static $inject = ["toolbarUtils", "actionUtils"];
    static key: string = "schemaListAction";

    constructor(toolbarUtils, actionUtils) {
        let actionModel: IActionModel = {
            key: List.key,
            type: ActionType.list,
            title: "SCHEMA管理",
            icon: "view-module",
            list: {
                columns: [
                    actionUtils.columnBuilder("<span>{{::item.id}}</span>", "ID", "id").toValue(),
                    actionUtils.columnBuilder("<span>{{::item.key}}</span>", "KEY").toValue(),
                    actionUtils.columnBuilder("<span>{{ ::item.type }}</span>", "模块类型").toValue(),
                    actionUtils.columnBuilder(`<span>{{ ::item.description }}</span>`, "描述").toValue(),
                ],
                showPagination: true,
                searchActionKey: "",
                showRefreshBtn: true,
                showSearchBtn: true,
                showSearchPanel: false,
                toolbars: [],
                itemToolbars: []
            },
            // itemActions: [{key: ModuleEdit.key}, {key: ModuleDelete.key}],
            actions: [Add.key],
            interfaces: [{
                key: "modulesList",
                method: MethodType.GET,
                address: "",
                port: null,
                path: "schemas",
                jpp: {
                    set: {
                        "/total": "/count",
                        "/rows": "/rows"
                    },
                    copy: {},
                    move: {},
                    del: {}
                },
                isRestful: true
            }]
        };

        return actionModel;
    }
}

class Add {
    static key: string = "schemaAddAction";

    constructor() {
        let actionModel: IActionModel = {
            key: Add.key,
            type: ActionType.form,
            title: "新建SCHEMA",
            icon: "add",
            refreshList: true,
            form: {
                dataSchema: dataSchema,
                formSchema: [{
                    key: "key",
                    type: "text",
                    htmlClass: "md-block"
                }, {
                    key: "type",
                    type: "select",
                    titleMap: [{
                        name: "DATA",
                        value: "DATA"
                    }, {
                        name: "FORM",
                        value: "FORM"
                    }, {
                        name: "ACTION",
                        value: "ACTION"
                    }]
                }, {
                    key: "description",
                    type: "textarea",
                    htmlClass: "md-block"
                }]
            },
            closeDialog: true,
            interfaces: [{
                key: "schemaAdd",
                method: MethodType.POST,
                address: "",
                port: null,
                path: "schemas",
                isRestful: true
            }]
        };

        return actionModel;
    }
}

export default (module: ng.IModule) => {
    const services: Array<any> = [List, Add];

    _.each(services, (ser)=> {
        module.service(ser.key, ser);
    });
}



